.App {
  text-align: center;
}



.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.app-container {
  height:100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background: linear-gradient(
    rgba(0, 0, 0, 0.15), 
    rgba(0, 0, 0, 0.15));
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.home-page{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  height:100vh;
  width: 100%;
}


.logo {
  width: 90%;
}

.logo-div {
  display: flex;
  justify-content: center;
  align-items: center;
}
.scroll-box {
  height: 90vh;
  width: 95%;
  overflow: auto;
}

.two-column {
  min-width: 300px;
  justify-content: center;
  align-items: center;
  display: flex;
  width: 50%;
}



.first-column {
  height: 100%;
  width: 100%;
  background: linear-gradient(
    rgba(0, 0, 0, 0.25), 
    rgba(0, 0, 0, 0.25)),
    url('./photos/deepSpaceGrid.jpeg');
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  
}
.second-column {
  width: 50%;
}

.myVideo {
  position: fixed;
  /* right: 0;
  bottom: 0; */
  min-width: 100%; 
  min-height: 100%;
  z-index: -1;
}

::-webkit-scrollbar {
  width: 12px;
  height: 12px;
  }
  
  ::-webkit-scrollbar-track {
  border: 1px solid yellowgreen;
  border-radius: 10px;
  }
  
  ::-webkit-scrollbar-thumb {
  background: yellowgreen;  
  border-radius: 10px;
  }
  
  ::-webkit-scrollbar-thumb:hover {
  background: #88ba1c;  
  }